<template>
   <div class='zhdlu'>
       <p>
           账号<input type="text" v-model="zh">
       </p>
        <p>
           密码<input type="text" v-model="mi">
       </p>
       <el-button type="info" @click="tj">登录</el-button>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
           zh:"",
           mi:"",
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
       tj(){
            this.$http.post("/api/app/login",{mobile:this.zh,password:this.mi,type:1}).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
                 this.$router.push("/sy")
                this.$toast.success(res.data.msg);
            }else{
                 this.$toast.fail(res.data.msg);
            }
         })
       }
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {

   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
