<template>
   <div class='sy'>
       <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item v-for="(item,index) of banner" :key="index">
                <img :src="item.banner_img" alt="">
               
            </van-swipe-item>
        </van-swipe>
        <div class="lxy_nei" v-for="(item,index) of list" :key="index">
            <div class="lxy_ming" v-if="item.channel_info.type == 3">
                <h3>{{ item.channel_info.name }}</h3>
               <ul>
                    <li v-for="(c,index) of item.list" :key="index">
                        <img :src="c.teacher_avatar" alt="">
                        <div>
                            <p>{{ c.teacher_name }}</p>
                            <p>{{ c.introduction }}</p>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="lxy_jing" v-if="item.channel_info.type == 1">
                <h3>{{ item.channel_info.name }}</h3>
                <ul>
                    <li v-for="(c,index) of item.list" :key="index">
                        <img :src="c.cover_img" alt="">
                        <div>
                            <p>{{ c.sales_num }}已报名</p>
                            <p>{{ c.price | jq }}</p>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="lxy_re" v-if="item.channel_info.type == 6">
                <h3>{{ item.channel_info.name }}</h3>
                <ul>
                    <li v-for="(c,index) of item.list" :key="index">
                        <img :src="c.thumb_img" alt="">
                        <p>{{ c.description }}</p>
                    </li>
                </ul>
            </div>
        </div>
        

   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
           banner:{},
           list:{},
       };
   },
   filters:{
       jq(val){
           if(val == 0){
               return "免费"
           }else{
               (val/100).toFixed(2)
           }
           return val
       }
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
      async lb(){
          let { data:res } = await this.$http.get("/api/app/recommend/appIndex")
          this.list = res.data
           console.log(this.list)
       }
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.$http.get("/api/app/banner").then((res)=>{
        //    console.log(res)
           this.banner = res.data.data
        //    console.log(this.banner)
       })
    //    this.$http.get("/api/app/recommend/appIndex").then((res)=>{
    //     //    console.log(res)
    //        this.list = res.data.data
    //        console.log(this.list)
    //    })
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {
       this.lb()
   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
