*{
    margin: 0;
    padding: 0;
    list-style:none;
}
    .my-swipe .van-swipe-item{
    width: 100%;
    height: 200px;
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
    }
    .my-swipe .van-swipe-item >img{
        width: 100%;
        height: 100%;
    
    }
    .lxy_nei{
        width: 100%;

    }
    h3{
        text-align: left;
    }
    .lxy_nei >.lxy_ming{
        background: lightgreen;
    }
   .lxy_nei >.lxy_ming img{
       width: 50px;
       height: 50px;
       border-radius: 50%;
   }
    .lxy_nei >.lxy_ming ul li{
        height: 60px;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .lxy_nei >.lxy_ming ul li >div{
        width: 70%;
    }
    .lxy_nei >.lxy_ming ul li >div p{
        width: 100%;
        text-align: left;
    }
   .lxy_nei >.lxy_jing{
       background: lime;
   }
   .lxy_nei >.lxy_jing img{
       width: 50px;
       height: 50px;
   }
   .lxy_nei >.lxy_re{
       background: cyan;
   }
   .lxy_nei >.lxy_re img{
       width: 50px;
       height: 50px;
   }
   .lxy_nei >.lxy_re ul li{
       width: 100%;
       height: 146px;
       display: flex;
       align-items: center;
       justify-content: space-around;
   }
   .lxy_nei >.lxy_re ul li p{
       width: 80%;
   }
</style>