<template>
   <div class='dlu'>
       <p>
           新建密码<input type="text" v-model="mi">
       </p>
        <p>
           确认密码<input type="text" v-model="qm">
       </p>
       <el-button type="info" @click="tj">提交</el-button>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
           mi:"",
           qm:"",
           phone:"",
           sms_code:"",
           code:""
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {
       
   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
       tj(){
            console.log(this.phone)
         let sjh = JSON.parse(localStorage.getItem("sjh"))
         let yzm = JSON.parse(localStorage.getItem("yzm"))
            console.log(sjh)
            if(this.mi == this.qm){
                    this.phone = sjh
                }else{
                    this.$toast.fail("确认密码和密码不一致");
                } 
            // this.phone = sjh 
            this.sms_code = yzm
            console.log(this.phone)
        this.$http.post("/api/app/password",{mobile:this.phone,password:this.mi,sms_code:this.sms_code,sms_type:'login'}).then((res)=>{
            console.log(res)
                
            if(res.data.code == 200){
                 this.$router.push("/sy")
                this.$toast.success(res.data.msg);
            }else{
                 this.$toast.fail(res.data.msg);
            }
         })
       }
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
