   methods: {
     lxy_d(){
         this.$http.post("/api/app/smsCode",{mobile:this.phone,sms_type:'login'}).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
               this.$toast.success(res.data.msg);
                window.localStorage.setItem("sjh",this.phone)
                //验证码倒计时
               let sj = 60;
               if (!this.timer) {
                  this.count = sj;
                  this.show = false;
                  this.timer = setInterval(() => {
                  if (this.count > 0 && this.count <= sj) {
                     this.count--;
                  } else {
                     this.show = true;
                     clearInterval(this.timer);
                     this.timer = null;
                  }
                  }, 1000)
                  }
            }else{
               this.$toast.fail(res.data.msg);
            }
         })
         // request.post("/api/app/smsCode",{mobile:this.phone,sms_type:'login'}).then(res=>{
